.buttonStyle {
  display: inline-block;
  background-color: #000;
  color: #fff;
  padding: 1.5rem 2.3rem;
  margin: 1rem 0;
  cursor: pointer;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease;
  -webkit-transition-delay: .6s;
       -o-transition-delay: .6s;
          transition-delay: .6s;
}
.heroSummary {
  font-size: 1.7rem;
  font-weight: 300;
  color: rgba(0,0,0,0.4);
  margin: 1rem 0;
}
.contentWrap {
  position: absolute;
  line-height: 1;
  margin-left: 8%;
  text-align: left;
  -webkit-animation: content-wrap-animation .5s linear;
          animation: content-wrap-animation .5s linear;
}
.herotitle {
  position: relative;
  font-size: 5rem;
  color: #191919;
  text-indent: -0.2rem;
}
.typeWritter {
  position: relative;
  color: #666;
}
.typeWritter::after {
  content: '';
  margin: auto;
  position: absolute;
  right: -4px;
  top: 10px;
  height: 1rem;
  width: 1px;
  height: 70%;
  background-color: #666;
  -webkit-animation: cursor-animation 1.5s step-end infinite;
          animation: cursor-animation 1.5s step-end infinite;
}
.hero-title-br {
  display: none;
}
@media screen and (max-width: 1200px) {
  .hero-title-br {
    display: block;
  }
}
@-webkit-keyframes cursor-animation {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes cursor-animation {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes content-wrap-animation {
  from {
    opacity: 0;
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
@keyframes content-wrap-animation {
  from {
    opacity: 0;
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}



