.neo-loading-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  opacity: .7;
  -webkit-transition: all .7s;
  -o-transition: all .7s;
  transition: all .7s;
  -webkit-animation: bg .7s ease;
          animation: bg .7s ease;
  z-index: 1000;
}
@-webkit-keyframes bg {
  from {
    opacity: 0;
  }
  to {
    opacity: .7;
  }
}
@keyframes bg {
  from {
    opacity: 0;
  }
  to {
    opacity: .7;
  }
}
.neo-loading-layout {
  position: absolute;
  height: 10px;
  width: 100px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
      transform: translateX(-50%);
  z-index: 1001;
}

.ball {
  display: inline-block;
  width: 4px;
  height: 4px;
  border-radius: 2px;
  position: absolute;
}

.ball-1 {
  -webkit-animation: ball-1 3s infinite;
  animation: ball-1 3s infinite;
}

.ball-2 {
  -webkit-animation: ball-2 3s infinite;
  animation: ball-2 3s infinite;
}

.ball-3 {
  -webkit-animation: ball-3 3s infinite;
  animation: ball-3 3s infinite;
}

.ball-4 {
  -webkit-animation: ball-4 3s infinite;
  animation: ball-4 3s infinite;
}

@-webkit-keyframes ball-1 {
  0% {
    -webkit-transform: translate3d(0px, 0, 0);
            transform: translate3d(0px, 0, 0);
  }
  50% {
    -webkit-transform: translate3d(100px, 0, 0);
            transform: translate3d(100px, 0, 0);
  }
  100% {
    -webkit-transform: translate3d(0px, 0, 0);
            transform: translate3d(0px, 0, 0);
  }
}

