      -ms-transform: translateY(-100%);
          transform: translateY(-100%);
}
/* player-fold */
.player-fold {
  position: relative;
  float: left;
  width: 20px;
  height: 100%;
  /* background-color: red;  */
}
.player-fold::after {
  content: '';
  position: absolute;
  top: 50%;
  /* left: 50%; */
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
      -ms-transform: translate(-50%, -50%) rotate(45deg);
          transform: translate(-50%, -50%) rotate(45deg);
