.greet-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: all .6s linear;
  -o-transition: all .6s linear;
  transition: all .6s linear;
}
.greet-content-layout {
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  right: 0;
  width: 50%;
  height: 100%;
}
.greet-flower-layout {
  position: absolute;
  width: 50%;
  height: 100%;
  overflow: hidden;
}


