.article {
  display: block;
  max-width: 50rem;
  padding: 10px;
  margin: 5rem auto;
  -webkit-transition: all 2s;
  -o-transition: all 2s;
  transition: all 2s;
  -webkit-animation: article-animation .3s linear;
          animation: article-animation .3s linear;
  cursor: pointer;
}
.article-title {
  float: left;
  font-size: 1.5rem;
  font-weight: 200;
  color: #666;
  line-height: 3rem;
  -webkit-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease;
}
.article:hover {
  -webkit-transform: translate3d(-3px, 0, 0);
          transform: translate3d(-3px, 0, 0);
}
.article-summary {
  padding: 3px 0;
  font-size: 1rem;
  font-weight: 200;
  color: #333;
}
.article-date {
  line-height: 3rem;
  display: block;
  float: right;
  color: #ddd;
}
.pending-home {
  -webkit-animation: pending-home 1.5s linear infinite;
          animation: pending-home 1.5s linear infinite;
  line-height: 1;
  margin: 10px 0;
  opacity: 1;
  color: #eaeaea;
  background-color: #eaeaea;
  -webkit-transition: all 1s linear;
  -o-transition: all 1s linear;
  transition: all 1s linear;
}
@-webkit-keyframes pending-home {
  0% {
    -webkit-transform: scale(1) translateY(0%);
            transform: scale(1) translateY(0%);
    opacity: 1;
  }
  50% {
    -webkit-transform: scale(.9) translateY(50%);
            transform: scale(.9) translateY(50%);
    opacity: 0;
  }
  0% {
    -webkit-transform: scale(1) translateY(0%);
            transform: scale(1) translateY(0%);
    opacity: 1;
  }
}
@keyframes pending-home {
  0% {
    -webkit-transform: scale(1) translateY(0%);
            transform: scale(1) translateY(0%);
    opacity: 1;
  }
  50% {
    -webkit-transform: scale(.9) translateY(50%);
            transform: scale(.9) translateY(50%);
    opacity: 0;
  }
  0% {
    -webkit-transform: scale(1) translateY(0%);
            transform: scale(1) translateY(0%);
    opacity: 1;
  }
}
@-webkit-keyframes article-animation {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -10%, 0);
            transform: translate3d(0, -10%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}
@keyframes article-animation {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -10%, 0);
            transform: translate3d(0, -10%, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}


