.app, .page {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
}
.container {
  width: 100%;
  height: 100%;
}
@media screen and (max-width:1100px) {
  .greet-flower-layout {
    display: none;
  }
  .greet-content-layout {
    width: 100%;
  }
}



