.header-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  z-index: 1000;
}
.logo {
  display: inline-block;
  font-size: 2rem;
  color: #333;
  font-weight: 400;
  padding: 1rem 10rem;
  -webkit-transition: all .6s ease;
  -o-transition: all .6s ease;
  transition: all .6s ease;
  -webkit-transition-delay: 1s;
       -o-transition-delay: 1s;
          transition-delay: 1s;
  -webkit-animation: logo-animation 1s ease;
          animation: logo-animation 1s ease;
}
.nav {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  right: 10rem;
  text-align: center;
  -webkit-animation: nav-animation 1s ease;
          animation: nav-animation 1s ease;
}
.nav a {
  display: inline-block;
  padding: 0 1rem;
  cursor: pointer;
  -webkit-transition: all .6s ease;
  -o-transition: all .6s ease;
  transition: all .6s ease;
  -webkit-transition-delay: 1s;
       -o-transition-delay: 1s;
          transition-delay: 1s;
  color: #333;
}
@-webkit-keyframes nav-animation {
  from {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  to {
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
  }
}
@keyframes nav-animation {
  from {
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
  to {
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
  }
}
@-webkit-keyframes logo-animation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;;
  }
}
@keyframes logo-animation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;;
  }
}
@media screen and (max-width: 1200px) {
  .logo {
    width: 100%;
    display: block;
    text-align: center;
    position: fixed;
    bottom: 0;
    padding: 0;
  }
  .nav {
    width: 100%;
    right: 0;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    line-height: 5rem;
  }
}
@media screen and (max-width: 350px) {
  .nav a {
    padding: 0 .5rem;
  }
}


